        <input class="font_14 login_input" type="text" placeholder="请输入楼盘名称" v-model="title"/>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">面积</span>
