        <input class="font_14 login_input" type="text" style="width: 20%;" placeholder="所在层数" v-model="louceng[0]"/>
        <span style="color: #000">/</span>
        <input class="font_14 login_input0" type="text" style="width: 20%;" placeholder="总层数" v-model="louceng[1]"/>
      </div>
      <div class="login_bar background_color_ffffff login_top login_bottom">
        <span class="font_14" style="padding-right: 10px;">户型</span>
        <select class="font_14 select-bar1" v-model="huxing[0]">
          <option>0</option>
          <option>1</option>
          <option>2</option>
          <option>3</option>
          <option>4</option>
          <option>5</option>
          <option>6</option>
          <option>7</option>
          <option>8</option>
          <option>9</option>
        </select>
        <span class="font_12 color_8b8b8b">室</span>
        <select class="font_14 select-bar1" v-model="huxing[1]">
          <option>0</option>
          <option>1</option>
          <option>2</option>
          <option>3</option>
          <option>4</option>
          <option>5</option>
          <option>6</option>
          <option>7</option>
          <option>8</option>
          <option>9</option>
        </select>
        <span class="font_12 color_8b8b8b">厅</span>
        <select class="font_14 select-bar1" v-model="huxing[2]">
          <option>0</option>
          <option>1</option>
          <option>2</option>
          <option>3</option>
          <option>4</option>
          <option>5</option>
          <option>6</option>
          <option>7</option>
          <option>8</option>
          <option>9</option>
        </select>
        <span class="font_12 color_8b8b8b">卫</span>
        <select class="font_14 select-bar1" v-model="huxing[3]">
          <option>0</option>
          <option>1</option>
          <option>2</option>
          <option>3</option>
          <option>4</option>
          <option>5</option>
          <option>6</option>
          <option>7</option>
          <option>8</option>
          <option>9</option>
        </select>
        <span class="font_12 color_8b8b8b">厨</span>
        <select class="font_14 select-bar1" v-model="huxing[4]">
          <option>0</option>
          <option>1</option>
          <option>2</option>
          <option>3</option>
          <option>4</option>
          <option>5</option>
          <option>6</option>
          <option>7</option>
          <option>8</option>
          <option>9</option>
        </select>
        <span class="font_12 color_8b8b8b">阳台</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">车库</span>
        <span class="check_box" :class="{selected: checked1}" @click="checked1=!checked1"></span>
        <span>有</span>
        <input class="font_14 login_input" style="width: 40%" type="text" placeholder="请输入车库面积" v-model="cheku"/>
        <span class="float_right">㎡</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">储藏室</span>
        <span class="check_box" :class="{selected: checked2}" @click="checked2=!checked2"></span>
        <span>有</span>
        <input class="font_14 login_input" style="width: 40%" type="text" placeholder="请输入储藏室面积"  v-model="chucang"/>
        <span class="float_right">㎡</span>
      </div>

      <div class="login_bar background_color_ffffff login_top login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">装修情况</span>
        <select class="font_14 select-bar" v-model="zhuangxiu">
          <option>豪装</option>
          <option>精装</option>
          <option>中装</option>
          <option>简装</option>
          <option>毛坯</option>
        </select>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">朝向</span>
        <select class="font_14 select-bar" v-model="chaoxiang">
          <option>东</option>
          <option>南</option>
          <option>西</option>
          <option>北</option>
        </select>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">区域</span>
        <select class="font_14 select-bar" v-model="area">
          <option>市区</option>
          <option>城东</option>
          <option>城南</option>
          <option>城西</option>
          <option>城北</option>
        </select>
      </div>
      <div class="login_bar background_color_ffffff login_top login_bottom">
        <span class="font_14 login_bottom" style="display: block;padding-bottom: 15px">点选房屋配置</span>
        <div>
          <div class="check-box-bar" v-for="(item,index) in label" :key="index">
            <span class="check_box" :class="{selected:item.checked}" @click="changeCheck(index)"></span>
            <span>{{item.name}}</span>
          </div>
        </div>
      </div>
      <div class="login_bar background_color_ffffff login_top login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">付款方式</span>
        <select class="font_14 select-bar" v-model="paytype">
          <option>押一付三</option>
          <option>季付</option>
          <option>半年付</option>
          <option>年付</option>
        </select>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">租金</span>
        <input class="font_14 login_input" style="width: 40%;" type="text" placeholder="请输入租金" v-model="price"/>
        <span class="float_right">元/月</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">房源介绍</span>
        <textarea class="font_14 login_input1" style="width: 100%" type="text" placeholder="请输入房源介绍" v-model="content"/>
      </div>
      <div class="login_bar background_color_ffffff login_top">
        <img :src="item" class="img_service" v-for="(item,index) in imgList" @click="choice(index)"/>
        <img src="static/img/icon_tjtp_tjps.png" class="img_service" @click="uploadImg"/>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'addRental',
		data() {
			return {
        userId:localStorage.getItem('userId'),
        uname:'',
        tell:'',
        metre:'',
        price:'',
        address:'',
        title:'',
        content:'',
        type:3,
        sameimg:'',
        imgList:[],
        louceng:[],
        zhuangxiu:'豪装',
        chaoxiang:'东',
        area:'市区',
        huxing:['0','0','0','0','0'],
        label:[{checked:false,name:'WIFI'},{checked:false,name:'冰箱'},{checked:false,name:'空调'},{checked:false,name:'洗衣机'},
              {checked:false,name:'床'},{checked:false,name:'沙发'},{checked:false,name:'衣柜'},{checked:false,name:'桌椅'},
              {checked:false,name:'电视'},{checked:false,name:'暖气'},{checked:false,name:'热水器'},{checked:false,name:'天然气'},
              {checked:false,name:'抽油烟机'},{checked:false,name:'可做饭'},{checked:false,name:'微波炉'},{checked:false,name:'电磁炉'},
              {checked:false,name:'燃气灶'}],
        paytype:'押一付三',
        checked1:false,
        checked2:false,
        cheku:'',
        chucang:'',
			}
		},
		methods:{
		  checkSubmit(){
		    if(!this.uname||!this.tell||!this.metre||!this.price||!this.address||!this.title||!this.content||!this.louceng[0]||!this.louceng[1]){
          this.$toast({
            message: '请将内容填写完整!',
            position: "bottom"
          });
        }else if(this.checked1&&!this.cheku||!this.checked1&&this.cheku){
          this.$toast({
            message: '请填写车库面积并勾选!',
            position: "bottom"
          });
        }else if(this.checked2&&!this.chucang||!this.checked2&&this.chucang){
          this.$toast({
            message: '请填写储藏室面积并勾选!',
            position: "bottom"
          });
        }else if(this.imgList.length<1){
          this.$toast({
            message: '至少上传1张图片!',
            position: "bottom"
          });
        }else{
          this.addRental();
        }
      },
      changeCheck(index){
        this.label[index].checked = !this.label[index].checked;
      },
      // choice(index){
      //   this.$dialog.confirm({
      //     message: '请选择切换或删除该张图片',
      //     confirmButtonText:'删除',
      //     cancelButtonText:'切换'
      //   }).then(() => {
      //     let newImgList = [];
      //     for(let i=0;i<this.imgList.length;i++){
      //       if(i!=index){
      //         newImgList.push(this.imgList[i]);
      //       }
      //     }
      //     this.imgList = newImgList;
      //   }).catch(() => {
      //     this.changeImg(index)
      //   });
      // },
      // changeImg(dataMap){
      //   let $this = this;
      //   dsBridge.call('uploadImg');
      //   dsBridge.register('addImgCallback', function(rpdata) {
      //     let imgList1 = [];
      //     for(let i =0;i<$this.imgList.length;i++){
      //       if(i==dataMap){
      //         imgList1.push(rpdata)
      //       }else{
      //         imgList1.push($this.imgList[i])
      //       }
      //     }
      //     $this.imgList = imgList1;
      //   })
      // },
      // uploadImg(){
      //   let $this = this;
      //   if(this.imgList.length==9){
      //     $this.$toast({
      //       message: '最多可以上传9张图片!',
      //       position: "bottom"
      //     });
      //   }else if(this.imgList.length<9){
      //     dsBridge.call('uploadImg');
      //     dsBridge.register('addImgCallback', function(rpdata) {
      //       $this.imgList.push(rpdata);
      //     })
      //   }
      // },
      choice(index){
        this.$dialog.confirm({
          message: '请选择切换或删除该张图片',
          confirmButtonText:'删除',
          cancelButtonText:'切换'
        }).then(() => {
          let newImgList = [];
          for(let i=0;i<this.imgList.length;i++){
            if(i!=index){
              newImgList.push(this.imgList[i]);
            }
          }
          this.imgList = newImgList;
        }).catch(() => {
          this.changeImg(index)
        });
      },
      changeImg(dataMap){
        let $this = this;
        dsBridge.call('uploadImg');
        dsBridge.register('addImgCallback', function(rpdata) {
          if(rpdata.split(',').length>1){
            $this.$toast({
              message: '请上传一张图片',
              position: "bottom"
            });
          }else{
            let imgList1 = [];
            for(let i =0;i<$this.imgList.length;i++){
              if(i==dataMap){
                imgList1.push(rpdata)
              }else{
                imgList1.push($this.imgList[i])
              }
            }
            $this.imgList = imgList1;
          }
        })
      },
      uploadImg(){
        let $this = this;
        if(this.imgList.length==9){
          $this.$toast({
            message: '最多可以上传9张图片!',
            position: "bottom"
          });
        }else if(this.imgList.length<9){
          dsBridge.call('uploadImg');
          dsBridge.register('addImgCallback', function(rpdata) {
            if((rpdata.split(',').length+$this.imgList.length)>9){
              $this.$toast({
                message: '最多可以上传9张图片!',
                position: "bottom"
              });
            }else{
              for(let i=0;i<rpdata.split(',').length;i++){
                $this.imgList.push(rpdata.split(',')[i]);
              }
            }
          })
        }
      },
      addRental(){
		    let label = [];
		    for(let i=0;i<this.label.length;i++){
		      if(this.label[i].checked){
		        label.push(this.label[i].name);
          }
        }
        this.sameimg = this.imgList.join(',');
        let $this = this;
        let obj = {
          userid: $this.userId,
          uname: $this.uname,
          tell: $this.tell,
          metre: $this.metre,
          price: $this.price,
          address: $this.address,
          title: $this.title,
          type: $this.type,
          content: $this.content,
          sameimg: $this.sameimg,
          louceng: $this.louceng.join('/'),
          zhuangxiu: $this.zhuangxiu,
          chaoxiang: $this.chaoxiang,
          area: $this.area,
          huxing: $this.huxing.join(','),
          label: label.join(','),
          paytype: $this.paytype,
          cheku: $this.cheku,
          chucang: $this.chucang,
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/addsamecity", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '发布成功!',
              position: "bottom"
            });
            $this.$router.back(-1);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .check-box-bar{
    width: 25%;
    height: 30px;
    font-size: 12px;
    display: inline-flex;
    line-height: 30px;
    align-items: center;
  }
  .selected{
    background-color: #c1140f;
  }
  .check_box{
    border: 1px solid #c1140f;
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 2px;
    margin-right: 5px;
  }
  .select-bar1{
    background-color: #f4f4f4;
    padding: 5px;
    border-radius: 5px;
    color: #8a8a8a;
    border: 0;
    font-size: 12px;
  }
  .select-bar{
    width: 100px;
    background-color: #f4f4f4;
    padding: 5px;
    border-radius: 5px;
    color: #8a8a8a;
    margin-left: 8%;
    border: 0;
  }
  .img_service{
    width: 32.3%;
  }
  .login_bottom{
    border-bottom: 1px solid #eeeeee;
  }
  .login_input1{
    margin-left: 0%;
    border: 0;
  }
  .login_input{
    margin-left: 8%;
    border: 0;
  }
  .login_input0{
    border: 0;
  }
  .width_5{
    width: 5%;
  }
  .login_top{
    border-top: 5px solid #eeeeee;
  }
  .login_bar{
    padding: 15px 15px;
  }
  .login_box{
    margin-top: 50px;
  }
</style>



